@font-face {
    font-family: 'gitlinkicons';
    src: url('../img/fonts/gitlinkicons.eot?16268704');
    src: url('../img/fonts/gitlinkicons.eot?16268704#iefix') format('embedded-opentype'),
    url('../img/fonts/gitlinkicons.woff2?16268704') format('woff2'),
    url('../img/fonts/gitlinkicons.woff?16268704') format('woff'),
    url('../img/fonts/gitlinkicons.ttf?16268704') format('truetype'),
    url('../img/fonts/gitlinkicons.svg?16268704#gitlinkicons') format('svg');
    font-weight: normal;
    font-style: normal;
}

/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
